import React, { useMemo } from 'react';
import { StickerPlugin, unitsConverter } from '@ali/4ever-bamboo';
import { StickerPlugin as BiStickerPlugin } from '@ali/4ever-bi';

const { StickerPlaceholder } = BiStickerPlugin.components;
const { DEFAULT_SZ, StickerItem, getStickerStyle } = StickerPlugin;

function StickerWrapper(props) {
  const { node, sticker } = props;
  const { sz = DEFAULT_SZ, highlight } = useMemo(
    () => getStickerStyle(node),
    [node],
  );

  return (
    <StickerItem
      key={node.key}
      code={node.data.code}
      backgroundColor={highlight}
      sz={unitsConverter.toPX(`${sz * 1.5}pt`)}
      placeholder={<StickerPlaceholder readonly {...props} />}
      sticker={sticker}
    />
  );
}

export default React.memo(StickerWrapper);
